const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};

const inputTheme = document.querySelector('#theme-switch-toggle');
const bodyTheme = document.querySelector('body');
bodyTheme.setAttribute('data-theme', Theme.LIGHT);

inputTheme.addEventListener('change', function () {
  if (this.checked) {
    bodyTheme.setAttribute('data-theme', Theme.DARK);
    localStorage.setItem('data-theme', Theme.DARK);
  } else {
        bodyTheme.setAttribute('data-theme', Theme.LIGHT);
    localStorage.setItem('data-theme', Theme.LIGHT);
  }
})

console.log('bodyTheme', bodyTheme);